.contacts {
  padding: 44px 30px 39px 30px;
  margin: 0 auto;
  background-image: url("../img/decoration/zigzag-small.svg");
  background-repeat: repeat-x;
  background-position: top;

  @include tablet() {
    padding: 73px 50px 57px 50px;
    background-image: url("../img/decoration/zigzag-medium.svg");
  }

  @include desktop() {
    display: grid;
    grid-template-columns: 40% 60%;
    padding: 94px 50px 105px 113px;
    background-image: url("../img/decoration/zigzag-large.svg");
    background-repeat: no-repeat;
    background-size: 1050px;
    max-width: 1150px;
  }
}

.contacts__title {
  display: flex;
  font-size: 25px;
  font-weight: 700;
  color: $title-dark;
  margin: 0;

  @include tablet() {
    font-size: 43px;
    line-height: 50px;
    align-items: center;
    width: 47px;
    height: 81px;
    padding: 0 625px 0 0;
    background-image: url("../img/decoration/flag.svg");
    background-repeat: no-repeat;
    background-position: right;
  }

  @include desktop() {
    font-size: 53px;
    line-height: 57px;
    padding: 46px 0 0 111px;
    background-position: left;
  }
}

