.contacts__list {
  text-align: left;
  margin: 0;

  @include tablet-only() {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
    column-gap: 25px;
  }
}

.contacts__item {
  display: flex;
  margin-top: 28px;

  @include tablet() {
    margin-top: 40px;
  }

  @include desktop() {
    margin-top: 28px;
  }
}

.contacts__item-title {
  width: 76px;
  font-size: 14px;
  margin-right: 20px;

  @include tablet() {
    width: 81px;
    font-size: 17px;
  }

  @include desktop() {
    width: 82px;
  }
}

.contacts__item-text {
  width: 100%;
  font-size: 14px;
  margin: 0;

  &--mail {
    color: $text-dark;
    text-decoration: underline $color-aqua-line;
  }

  @include tablet() {
    font-size: 17px;
  }
}

.contacts__map {
  height: 454px;
  margin: 62px -30px 0 -30px;

  @include tablet() {
    margin: 49px -50px 0 -50px;
  }

  @include desktop() {
    width: 100%;
    grid-area: 1 / 2 / 4 / 3;
    margin: 0;
  }
}

.contacts__map-item {
  height: 100%;
  width: 100%;
  border: none;
  display: block;
}

.contacts__button {
  margin-top: 42px;
  width: 100%;

  @include tablet() {
    margin-top: 55px;
    width: 230px;
  }

  @include desktop() {
    margin-top: 85px;
    margin-left: 64px;
  }
}
